@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Oswald&display=swap');

.wrapper1 {
    width: 100%;
    position: relative;
    border-top: 1px solid;
    margin-top: 100px;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 100px;
    align-items: center;
}
.wrapperItem1, .wrapperItem2 {
    font-family: 'Oswald', sans-serif;
}
.wrapper-item{
    display: flex;
}
.head {
    position: absolute;
    margin-bottom: 300px;
    font-size: 35px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
}

.wrapperItem1, .wrapperItem2 {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    flex-wrap: nowrap;
}
.wrapperItem2 .block {
    &:last-child {
        border-right: none;
    }
}
.wrapperItem1 .block {
    &:first-child {
        border-left: none;
    }
}
.block {
    width: 250px;
    border-left: 1px solid rgba(53, 53, 53, 0.186);
    border-right: 1px solid rgba(53, 53, 53, 0.186);
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    & h2 {
        font-size: 50px;
        font-weight: 600;
        display: flex;
        justify-content: flex-end;
        align-items: end;
        & span {
            font-size: 20px;
            position: relative;
            bottom: 5px;
        }
    }
    & p {
        text-align: center;
        color: rgba(124, 124, 124, 0.46);
    }
    margin-bottom: 100px;
    
    
}


@media only screen and (max-width: 943px){
    .block{
        width: 12rem;
    }
}
@media only screen and (max-width: 630px){
    .block{
        width: 8rem;
        & h2 {
            font-size: 35px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 15px;
            }
        }
    }
}
@media only screen and (max-width: 510px){
    .wrapper1 {
        margin-top: 0;       
    }
    .block{
        width: 12.5rem;
        height: 140px;
        & h2 {
            font-size: 35px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 15px;
                position: relative;
                top: 0;
                padding-left: 2px;
            }
        }
        & p {
            width: 150px;
            font-size: 15px;
        }
    }
    .wrapper-item {
        display: block;
    }
.wrapperItem1{
    margin-top: 100px;
     .block:last-child {
        border-right: none
    }
}
.wrapperItem2{
    margin-top: 0;
    .block:first-child {
        border-left: none;
    }
}
}
@media only screen and (max-width: 400px){
    .head{
        font-size: 30px;
    }
    .block{
        width: 10.5rem;
        height: 140px;
        & h2 {
            font-size: 30px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 15px;
                position: relative;
                top: 0;
                padding-left: 2px;
            }
        }
        & p {
            width: 130px;
            font-size: 13px;
        }
    }
}
@media only screen and (max-width: 321px){
    .head {
        font-size: 30px;
    }
    .block{
        width: 8.5rem;
        & h2 {
            font-size: 30px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 15px;
                position: relative;
                top: 0;
                padding-left: 2px;
            }
        }
        & p {
            font-size: 13px;
        }
    }
}