

  
  
  .navigationMenu {
    // margin-left: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid;
    margin: 0 auto;
  }
  nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    position: relative;
    display: flex;
    justify-content: center;
    gap: 100px;
    }
    
  nav ul li {
    display:inline-block;
    background-color: #ffffff;
    }
  
  nav a {
    display:block;
    padding:0 10px; 
    color:#000000;
    font-size:17px;
    line-height: 50px;
    text-decoration:none;
    font-weight:300 ;
    font-family:Arial, Helvetica, sans-serif;
  }
    

  li {
    border-radius: 5px;
    transition: background 0.2s;
   
    ul {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      padding: 0;
      border-radius: 5px;
      transition: opacity 0.2s, visibility 0.2s;
      
      li {
        margin: -2px 0 0 -2px;
        a {
          color: #000000;
        }
      }
    }
    &:hover {
      ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 700px;
        height: 250px;
        column-gap: 10px;
        visibility: visible;
        opacity: 1;
        background-color: #eeeeee;
        z-index: 0;
        margin-left: -200px; 
        margin-top: 10px; 
        .liTop {
          position: absolute;
          top: 100px;
        }
        li {
          position: relative;
          width: 300px;
          top: 2px;
          margin-left: 10px;
          height: 100px;
        background-color: #eeeeee;

          ul {
            position: absolute;
            background-color: #eeeeee;
            width: 0;
            li {
              width: 325px;
              position: absolute;
              left: 195px;
              top: -15px; 
              
              a {
                font-size: 13px;
                font-weight: 300;
                line-height: 25px;
              }
            }
            
          }
        }
      }
    }
  }  

.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  // box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
  background-color: #ffffff !important;
  border: 0;
}

