.dropdown {
  position: relative;
    // top: 50px;
  margin: 0 auto;
  width: 220px;
  // height: 100vh;
  // border: 1px solid #666;
 background: #ffffff;

}

.dropdown__text {
 color: rgb(0, 0, 0);
 padding: 10px 16px;
  cursor: pointer;
height: auto;
font-weight: 400;
font-size: 20px;
}

.dropdown__text:hover {
  color: rgb(0, 0, 0);
  font-weight: 600;
}

.dropdown__text:after {
  content: "";
  transition: all .3s;
  border: solid #ccc;
  border-width: 0 1px 1px 0;
float: right;
margin-top: 10px;
margin-right: 10px;
  padding: 5px;
    -webkit-transform: rotate(45deg);
  // height: 200px;
}

.dropdown.active .dropdown__text:after {
margin-top:15px;
-webkit-transform: rotate(-135deg);

}

.dropdown__items {
 position: relative;
visibility: hidden;
opacity: 0;
 max-height: 0px;
 transition: max-height 0.6s ease;
 font-size: 15px;
 color: #000000;
 font-weight: 400;
}


.dropdown.active .dropdown__items {
 visibility: visible;

  border-top: 1px solid #3c3c3c59;
height: 1000px;
 min-height: 400px;
  opacity: 1;
 transition: max-height 0.7s, opacity 3s, visibility 4s ease;



}

.dropdown__item {
  cursor: pointer;
 padding: 10px 18px;
  
} 

.dropdown__item:not(:last-child) {
  border-bottom: 1px solid #3c3c3c1a;
  
}



.dropdown__item:hover {
  font-weight: 500;
}
  
